import browserHistory from './browser'
import { parsePath, createPath } from 'history'
import { serialize, deserialize } from './url-parser'
import { history } from '../../../core/backbone'

const browserURLManager = {
  location: () => {
    const handler = browserHistory.handler
    const location = Object.assign(
      {},
      history().useHash
        ? parsePath(handler.location.hash.substring(1))
        : handler.location,
    )
    location.searchParams = new URLSearchParams(location.search)
    Object.defineProperty(location, 'search', {
      get: () => location.searchParams.toString(),
    })

    Object.defineProperty(location, 'toObject', {
      value: () => {
        if (history().useHash) {
          return {
            pathname: handler.location.pathname,
            search: handler.location.search,
            hash: createPath(location),
          }
        }
        return {
          pathname: handler.location.pathname,
          hash: handler.location.hash,
          search: location.search,
        }
      },
    })

    return location
  },
  get() {
    return deserialize(browserURLManager.location().searchParams.get('f') || '')
  },
  set(obj) {
    const url = browserURLManager.location()
    const searchParams = url.searchParams
    searchParams.set('f', serialize(obj))
    return url
  },
}

export function updateState(name) {
  const microfrontendState = browserURLManager.get()
  browserHistory.listeners[name].push({
    ...(microfrontendState[name] || { pathname: '/' }),
    state: { rehydration: true },
  })
}

const manager = {
  listen({ state = {} }, action) {
    // it was fired by a microfrontend(using update method)?
    // if so, we must ignore it to avoid an infinite loop
    if (action === 'PUSH' && state.rehydration) return
    //call each microfrontend pushing its new state
    Object.keys(browserHistory.listeners).forEach(updateState)
  },
  update(state) {
    const current = browserURLManager.get()
    current[state.name] = state
    const modified = browserURLManager.set(current)
    browserHistory.handler.push({
      ...modified.toObject(),
      state: { rehydration: true },
    })
  },
}

// we only want one per browser
if (typeof browserHistory.manager === 'undefined') {
  browserHistory.manager = manager
  browserHistory.handler.listen(manager.listen)
}

//helper method to help with testing
export const setupTests = handler => {
  browserHistory.handler = handler
  browserHistory.handler.listen(manager.listen)
}

export default browserHistory.manager
