/**
 * Based on `react-popper`
 * https://github.com/popperjs/react-popper
 *
 * TODO: install the react-popper when react-datepicker was updated to the same version 2.2.3+
 */
import { useLayoutEffect, useRef, useMemo, useState, useEffect } from 'react';
import { createPopper as defaultCreatePopper } from '@popperjs/core';
import isEqual from 'react-fast-compare';

const fromEntries = entries => entries.reduce((acc, [key, value]) => {
  acc[key] = value;
  return acc;
}, {});

const useIsomorphicLayoutEffect = typeof window !== 'undefined' && window.document && window.document.createElement ? useLayoutEffect : useEffect;
const EMPTY_MODIFIERS = [];

const usePopper = (referenceElement, popperElement, options = {}) => {
  const prevOptions = useRef(null);
  const optionsWithDefaults = {
    onFirstUpdate: options.onFirstUpdate,
    placement: options.placement || 'bottom',
    strategy: options.strategy || 'absolute',
    modifiers: options.modifiers || EMPTY_MODIFIERS
  };
  const [state, setState] = useState({
    styles: {
      popper: {
        position: optionsWithDefaults.strategy,
        left: '0',
        top: '0'
      }
    },
    attributes: {}
  });
  const updateStateModifier = useMemo(() => ({
    name: 'updateState',
    enabled: true,
    phase: 'write',
    fn: ({
      state
    }) => {
      const elements = Object.keys(state.elements);
      setState({
        styles: fromEntries(elements.map(element => [element, state.styles[element] || {}])),
        attributes: fromEntries(elements.map(element => [element, state.attributes[element]]))
      });
    },
    requires: ['computeStyles']
  }), []);
  const popperOptions = useMemo(() => {
    const newOptions = {
      onFirstUpdate: optionsWithDefaults.onFirstUpdate,
      placement: optionsWithDefaults.placement,
      strategy: optionsWithDefaults.strategy,
      modifiers: [...optionsWithDefaults.modifiers, updateStateModifier, {
        name: 'applyStyles',
        enabled: false
      }]
    };

    if (isEqual(prevOptions.current, newOptions)) {
      return prevOptions.current || newOptions;
    } else {
      prevOptions.current = newOptions;
      return newOptions;
    }
  }, [optionsWithDefaults.onFirstUpdate, optionsWithDefaults.placement, optionsWithDefaults.strategy, optionsWithDefaults.modifiers, updateStateModifier]);
  const popperInstanceRef = useRef();
  useIsomorphicLayoutEffect(() => {
    if (popperInstanceRef.current) {
      popperInstanceRef.current.setOptions(popperOptions);
    }
  }, [popperOptions]);
  useIsomorphicLayoutEffect(() => {
    if (referenceElement == null || popperElement == null) {
      return;
    }

    const createPopper = options.createPopper || defaultCreatePopper;
    const popperInstance = createPopper(referenceElement, popperElement, popperOptions);
    popperInstanceRef.current = popperInstance;
    return () => {
      popperInstance.destroy();
      popperInstanceRef.current = null;
    };
  }, [referenceElement, popperElement, options.createPopper]);
  return {
    state: popperInstanceRef.current ? popperInstanceRef.current.state : null,
    styles: state.styles,
    attributes: state.attributes,
    update: popperInstanceRef.current ? popperInstanceRef.current.update : null,
    forceUpdate: popperInstanceRef.current ? popperInstanceRef.current.forceUpdate : null
  };
};

export default usePopper;