import React from 'react';
import { withContext, useFeature } from '@resultadosdigitais/front-hub/react';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import { Theme, RDStationMarketing, Logo as LegacyLogo } from '@resultadosdigitais/tangram-components';
import { LogoMarketing } from '@resultadosdigitais/tangram-react-icons';

import InteropStyle from './components/InteropStyle';
import PrimaryMenu from './organisms/PrimaryMenu';
import SecondaryMenu from './organisms/SecondaryMenu';
import AccountMenu from './organisms/AccountMenu';

const Header = styled.header`
  display: flex;
  width: 100%;
  justify-content: space-between;

  background-color: var(--color-white);
  box-shadow: var(--shadow-sm);

  .wrapper {
    display: flex;

    &:first-child {
      flex-basis: calc(100% - 321px); /* 321px = account menu size */
    }

    &:last-child ul {
      margin: 0 var(--size-spacing-04);
    }
  }
`;

const Logo = styled.a`
  margin-right: var(--size-spacing-04);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--size-spacing-04);

  &:focus,
  &:active {
    outline: none;
  }
`;

const App = ({ user }) => {
  const { Enabled, Disabled } = useFeature();
  const { account } = user;
  const { t } = useTranslation();

  return (
    <>
      <Enabled feature="account:design:rebranding">
        <Theme value={Theme.kinds.lina}>
          <InteropStyle />
          <Header>
            <div className="wrapper">
              <Logo data-cypress="navigation" rel="noopener noreferrer" hint={t('goToHomePage')} href="/">
                <RDStationMarketing />
              </Logo>
              <PrimaryMenu createdAt={account.plan.createdAt} trialRemainingDays={account.trialRemainingDays} />
            </div>

            <div className="wrapper">
              <SecondaryMenu />
              <AccountMenu
                data-cypress="navigation"
                userEmail={user.email}
                userFirstName={user.firstName}
                userLastName={user.lastName}
                accountName={account.name}
                accountId={account.id}
              />
            </div>
          </Header>
        </Theme>
      </Enabled>
      <Disabled feature="account:design:rebranding">
        <Theme value={Theme.kinds.marketing}>
          <InteropStyle />
          <Header>
            <div className="wrapper">
              <LegacyLogo data-cypress="navigation" rel="noopener noreferrer" hint={t('goToHomePage')} href="/">
                <LogoMarketing title={`${t('goToHomePage')} | RDStation Marketing`} />
              </LegacyLogo>
              <PrimaryMenu createdAt={account.plan.createdAt} trialRemainingDays={account.trialRemainingDays} />
            </div>

            <div className="wrapper" style={{ position: 'relative' }}>
              <SecondaryMenu />
              <AccountMenu
                data-cypress="navigation"
                userEmail={user.email}
                userFirstName={user.firstName}
                userLastName={user.lastName}
                accountName={account.name}
                accountId={account.id}
              />
            </div>
          </Header>
        </Theme>
      </Disabled>
    </>
  );
};

export default withContext(App);
