import browserHistory from './browser'
import browserManager, { updateState } from './browser-manager'

export function listen(name, history) {
  browserHistory.listeners[name] = history
  const unListen = history.listen(({ state = {}, ...params }, action) => {
    // This action was fired by browser-manager?
    // If so, we must ignore it to avoid an infinite loop
    if (action === 'PUSH' && state.rehydration) return
    browserManager.update({
      ...params,
      name,
    })
  })
  // first time we need sync microfrontend url with browser history
  updateState(name)
  return () => {
    unListen()
    delete browserHistory.listeners[name]
  }
}
