import { communication } from '../../../../core/backbone'
import { parseToSafeJson } from '../../../../core/security'
import { useMemo, useEffect, useContext } from 'react'
import { FronthubContext } from 'front-hub-context-api'
import { CONTEXT } from '../../../../constants'

const TOPIC_DEFAULT = 'fronthub'

const communicationContext = communication()

function getContext(microfrontend) {
  if (!communicationContext[microfrontend]) {
    communicationContext[microfrontend] = {}
  }
  return communicationContext[microfrontend]
}

function register(microfrontend, name, callback) {
  const context = getContext(microfrontend)
  if (!context[name]) {
    context[name] = callback
  }
}

const microFrontendListeners = []

export default function useCommunication(topic = TOPIC_DEFAULT) {
  const {
    [CONTEXT]: { configuration },
    events: { listeners, emitters },
  } = useContext(FronthubContext)

  const eventTopic = eventName => topic + ':' + eventName
  const mapWithTopic = list =>
    list.map(eventName =>
      eventName.includes(':') ? eventName : eventTopic(eventName),
    )

  const listenersFromConfiguration = mapWithTopic(listeners)
  const emittersFromConfiguration = mapWithTopic(emitters)

  return {
    useListener(eventName, callback, deps = [0]) {
      const eventPlusTopic = eventTopic(eventName)

      const unListen = useMemo(() => {
        if (!listenersFromConfiguration.includes(eventPlusTopic)) {
          throw new Error(
            'front-hub: you must include event name ' +
              eventName +
              ' topic ' +
              topic +
              ' inside front-hub.config.js key events.listeners[], microfrontend name ' +
              configuration.name,
          )
        }

        register(configuration.name, eventPlusTopic, data => {
          microFrontendListeners.forEach(([ev, callback]) => {
            try {
              if (ev === eventPlusTopic) callback(data)
            } catch (e) {
              console.error(
                'front-hub: Microfrontend ' +
                  configuration.name +
                  ' event ' +
                  eventName +
                  ' topic ' +
                  topic +
                  ' error' +
                  e,
              )
            }
          })
        })

        const idx = microFrontendListeners.push([eventPlusTopic, callback])

        return () => microFrontendListeners.splice(idx - 1, 1)
      }, deps)

      useEffect(() => unListen, deps)

      return unListen
    },
    emit(eventName, data) {
      const eventPlusTopic = eventTopic(eventName)
      if (!emittersFromConfiguration.includes(eventPlusTopic))
        throw new Error(
          'front-hub: you must include emitter name ' +
            eventName +
            ' topic ' +
            topic +
            ' inside front-hub.config.js key events.emitters[], microfrontend name ' +
            configuration.name,
        )

      Object.keys(communicationContext).forEach(microfrontend => {
        const callback = communicationContext[microfrontend][eventPlusTopic]
        if (callback) {
          callback(parseToSafeJson(data))
        }
      })
    },
  }
}
