export function deserialize(url) {
  let csv = url.split('~'),
    name,
    path,
    query,
    hash
  const ret = {}
  try {
    do {
      ;[name, path, query, hash, ...csv] = csv
      if (typeof hash !== 'undefined')
        ret[decodeURIComponent(name)] = {
          name: decodeURIComponent(name),
          pathname: decodeURIComponent(path),
          search: decodeURIComponent(query),
          hash: decodeURIComponent(hash),
        }
    } while (csv.length)
  } catch (err) {
    console.error('front-hub: url component is wrong', err)
  }
  return ret
}

export function serialize(obj) {
  const fields = ['name', 'pathname', 'search', 'hash']
  return Object.keys(obj)
    .reduce(
      (acc, name) =>
        acc +
        fields.reduce(
          (str, field) =>
            str + '~' + encodeURIComponent(obj[name][field] || ''),
          '',
        ),
      '',
    )
    .substr(1)
}
