import communicationAPI from '../communication-api'

import { CONTEXT } from '../../../constants'
import { resolveProperty } from '../../../core/configuration'

export const methods = [
  'dispatchEvent',
  'delegate',
  'registerDelegate',
  'addEventListener',
]

export default function contextPublicApiFactory(fronthubInstance) {
  const privateApi = communicationAPI(fronthubInstance)
  const asPublic = resolveProperty(fronthubInstance)
  return methods.reduce(
    (acc, cur) => ({
      ...acc,
      [cur]: privateApi[cur],
    }),
    {
      context: asPublic('context', true),
      events: asPublic('events', false),
      rbac: asPublic('rbac', true),
      features: asPublic('features', true),
      [CONTEXT]: fronthubInstance,
    },
  )
}
