import { createBrowserHistory } from 'history'
import { history } from '../../../core/backbone'

const browser = history()

if (typeof browser.handler === 'undefined') {
  browser.handler = createBrowserHistory() //#1
  browser.useHash =
    typeof window !== 'undefined' &&
    typeof window.location.hash === 'string' &&
    window.location.hash !== ''
}

if (typeof browser.listeners === 'undefined') {
  browser.listeners = {}
}

export default browser
